// Search

#mobile-search {
  display: none;
}

#search {
  width: 265px / 1120px * 100%;
    display: table-cell;
    vertical-align: middle;
    @include clear; // clearfix

    .search-form {
        width: 265px;
        float: right;

        input {
            width: 230px;
            height: 35px;
            padding: 0 .3em 0 0;
            float: left;
            font: 18px $AkkuratProBold;
            background: none;
            color: $white;
            border: none;
            border-bottom: 1px solid $nu-purple-30;
            -webkit-appearance: none;

            &::-webkit-input-placeholder {
                color: $nu-purple-30;
            }

            &::-moz-placeholder {
                color: $nu-purple-30;
            }

            &:-moz-placeholder {
                color: $nu-purple-30;
            }

            &:-ms-input-placeholder {
                color: $nu-purple-30;
            }
        }

        button {
            float: left;
            cursor: pointer;
            height: 35px;
            width: 35px;
            background: url("#{$path-images}/icons/search.svg") no-repeat 0.5em 0.2em;
            background-size: 20px 20px;
            border: none;
            border-bottom: 1px solid $nu-purple-30;
            -webkit-appearance: none;
        }

        input:focus {
            outline: 0;
        }
    }
}

// media queries
@media screen and (max-width: 1145px) {

    #search {
        padding-right: 1rem;
    }
}

@media screen and (max-width: 768px) {

    header {
    position: relative;
  }

    #search {
        display: none;
    }

  #mobile-search {
    position: absolute;
    // top: 62px;
        margin-top: -6px; // artifically pushes search dropdown up
    left: 0;
    right: 0;
    z-index: 9998;
  }

  .search-form {
    width: 100%;
    height: auto;
    background: $nu-purple;
    float: none;
        position: relative;

    form {
      margin: 0 20px;
      height: 63px;
            position: relative;
      background-image: url('data:image/png;base64,');
    }

    input {
      width: 80%;
            height: 32px;
            margin: 15px 0;
            float: left;
            font: 18px $AkkuratProBold;
            color: $white;
      background: none;
      border: none;
    }

    button {
      float: left;
            cursor: pointer;
            height: 32px;
      width: 32px;
      padding: 10px 0 15px;
            background: url("#{$path-images}/icons/search.svg") no-repeat 1em .4em / 20px 20px;
      background-position: right center;
            border-bottom: none;
            position: absolute;
            right: 0;
            bottom: 18px;
    }

    .hide-label, input[type="hidden"] {
      visibility: hidden;
      display: none;
    }
  }
}